import { h } from 'preact'
import Form, {
  FormGroup, Input, TextArea, SubmitButton, SubmitForm, Select,
} from '@depack/form'
import { getUserData } from './../Auth/lib.js'
import CaptchaFormGroup from './../captcha.jsx'
import countries from './../countries.js';

export default class CommentForm extends SubmitForm {
  constructor() {
    super()
    this.fetchOptions = { credentials: 'include' }
    this.state.country_code = undefined
  }
  async componentWillMount() {
    try {
      const res = await fetch('https://freegeoip.app/json/', {})
      const { 'country_code': country_code } = await res.json()
      if (country_code) {
        this.setState({ country_code })
      }
    } catch (err) {
      // ok
    }
  }
  /**
   * @param {!Object} [props]
   * @param {Auth} [props.auth]
   */
  render({ onChange, host, auth, ...props }) {
    const { formLoading, error, success } = this.state

    const { picture, name } = getUserData(auth)

    return (h(Form,{...props, onSubmit:this.submit.bind(this), onChange:values => {
      this.reset()
      if(onChange) onChange(values)
    }},
      picture && h(Input,{value:picture,type:"hidden",name:"photo"}),
      h(Input,{value:auth.csrf,type:"hidden",name:"csrf"}),
      h(FormGroup,{'form-row':true,'col-2':true,label:"Name*",help:"This will appear on the website"},
        h(Input,{value:name,'col-10':true,name:"name"}),
      ),
      h(FormGroup,{label:"Country",help:"Where are you from?",'form-row':true,'col-2':true},
        h(Select,{value:this.state.country_code,'col-10':true,name:"country_code",options:countries.map(a => {
            return { value: a.code, title: a.name }
          }),defaultText:"select country"}),
      ),
      h(FormGroup,{help:auth.github_user ? 'GitHub username, sign out to remove' : 'Please sign in with GitHub','form-row':true,'col-2':true,label:"GitHub"},
        h(Input,{value:auth.github_user ? auth.github_user.html_url : null,'col-10':true,name:"github",disabled:true}),
      ),
      h(FormGroup,{'form-row':true,'col-2':true,label:"Comment*",help:"Please enter your opinion"},
        h('div',{'className':"col-10"},
          h(TextArea,{required:true,name:"comment"},
            `I think you're right/wrong because...`
          ),
        ),
      ),
      h(CaptchaFormGroup,{host:host,auth:auth}),
      h(SubmitButton,{loading:formLoading,type:"warning",confirmText:"Submit Data"}),
      error && `Error: ${error}`,
      success && `Comment has been submitted!`,
    ))
  }
}

/**
 * @suppress {nonStandardJsDocs}
 * @typedef {import('../..').Auth} Auth
 */
