import { h } from 'preact'
import { Component } from 'preact'
import callbackFetch from './../fetch.js'
import './style.css'
// import fetch from 'unfetch'

export default class List extends Component {
  constructor() {
    super()
    this.state = {
      /** @type {!Array<Comment>} */
      comments: [],
      page: 0,
      csrf: null,
    }
  }
  componentDidMount() {
    this.fetch()
  }
  /**
   * @param {string} [id]
   */
  fetch(id) {
    this.setState({ loading: true })
    const i = id ? `?id=${id}` : ''
    callbackFetch(`${this.props.host}/json-comments${i}`, (error, res) => {
      this.setState({ loading: false })
      if (error) {
        return this.setState({ error })
      }
      const { 'comments': comments, csrf } = res.json()
      this.setState({ comments: [...comments, ...this.state.comments], csrf })
    }, {
      credentials: 'include',
    })
  }
  render() {
    const { error, loading, comments, csrf } = this.state
    if (error)
      return (h('div',{},`Error loading list: `,error))
    if (loading)
      return (h('div',{},`Loading list...`))

    return          h('div',{'className':"CommentsList"},
      comments.map((comment) => {
        return h(Item,{key:comment._id,comment:comment,csrf:csrf,host:this.props.host,onRemove:(id) => {
          this.setState({
            comments: this.state.comments.filter(({ _id }) => {
              return _id != id
            }),
          })
        }})
      }))
  }
}

const Login = ({ github_user }) => {
  if (!github_user) return null
  return (h('span',{},` (`
    ,h('a',{'href':github_user.html_url},github_user.login),`)`
  ))
}
/**
 * @param {Object} opts
 * @param {WebsiteComment} opts.comment
 */
const Item = ({ comment: { _id, country, isAuthor, name, photo, comment, date, github_user }, onRemove, csrf, host }) => {
  return (h('div',{'className':"comment"},
    h('strong',{},name || 'Anonymous'),h(Login,{github_user:github_user}),
    country ? ` from ${country}`: '',
    ' ',`on `,h('em',{},new Date(date).toLocaleString()),` `,isAuthor && h('a',{'onClick':(e) => {
      e.preventDefault()
      const c = confirm('Are you sure you want to delete comment?')
      if (c) {
        callbackFetch(`${host}/remove-comment?csrf=${csrf}&id=${_id}`, (error, res) => {
          if (error) return alert(error)
          const { error: er } = res.json()
          if (er) alert(er)
          else if (res) onRemove(_id)
        }, {
          credentials: 'include',
        })
      }
      return false
    },'href':"#"},
      `Remove`
    ),
    h('div',{'className':"LCommentBlock"},
      photo && h('div',{},
        h('img',{'src':photo}),
      ),
      h('div',{},
        comment,
      ),
    ),
  ))
}

/**
 * @suppress {nonStandardJsDocs}
 * @typedef {import('../../').WebsiteComment} WebsiteComment
 */