import { h } from 'preact'
import LinkedIn from './../social/LinkedIn/index.jsx'
import GitHub from './../social/GitHub/index.jsx'
import User from './User.jsx'

/**
 * @param {Object} props
 * @param {Auth} props.auth
 */
const AppUser = ({ error, loading, auth, onSignOut, host }) => {
  if (error)
    return (h('div',{},`Error: `,error))
  if (loading)
    return (h('div',{},`Loading...`))
  // if (!auth.user)
  const loggedIn = auth.linkedin_user || auth.github_user
  return (h('div',{},
    !loggedIn && h('span',{'style':"display:block"},`To display the profile image and validate your GitHub profile, sign in. No advanced permissions are required other than default ones (no email). Your public LinkedIn ID remains unknown. You will not be able to delete/edit your comment as a guest. `,h('a',{'href':"/privacy-policy.html"},`Privacy Policy`)),

    h(User,{auth:auth,onSignout:onSignOut,host:host}),

    !auth.linkedin_user && h(LinkedIn,{host:host}),
    !auth.linkedin_user && ' ',
    !auth.github_user && h(GitHub,{host:host}),

  ))
}

export default AppUser