import { h } from 'preact'
import Captcha from './Auth/Captcha.jsx'
import {
  FormGroup, Input,
} from '@depack/form'

const CaptchaFormGroup = ({ auth, host }) => {
  if (auth.github_user || auth.linkedin_user) return null
  return (h(FormGroup,{'form-row':true, 'col-md-2':true, label:"Captcha", help:"Sign in to skip."},
    h(Captcha,{host:host,'col-sm-4':true,'col-md-3':true,'col-lg-2':true}),
    h(Input,{'col-sm-4':true,'col-md-3':true,name:"captcha-answer"}),
  ))
}


export default CaptchaFormGroup